.testimonials {
  & p {
    font-size: 13px;
    font-weight: 700;
    line-height: 150%;
    letter-spacing: 3px;
    color: #c0cdd7;
  }

  &__title {
    color: #253757;
    font-size: 32px;
    line-height: 150%;
    font-weight: 600;
    margin-bottom: 48px;

    @include on-tablet {
      margin-bottom: 56px;
    }

    @include on-small-desktop {
      margin-bottom: 85px;
    }
  }

  &__cards {
    @include page-grid;
  }

  &__card {
    height: 456px;
    background-color: #fff;
    border-radius: 16px;
    padding: 0 42px 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    font-family: 'open sans', Arial, Helvetica, sans-serif;
    font-weight: 600;
    font-size: 13px;
    line-height: 150%;
    text-align: center;
    grid-column: 1 / -1;
    margin: 24px 0;
    cursor: pointer;

    @include hover(scale, 1.1);

    @include on-tablet {
      padding: 32px 98px;
      margin: 30px 0;
    }

    @include on-small-desktop {
      grid-column: span 4;
      padding: 32px 45px;
    }

    @include on-large-desktop {
      padding: 32px 56px;
    }

    &-photo {
      margin: 75px 67px;
      position: relative;
      z-index: 1;

      @include on-tablet {
        padding-bottom: 75px 67px;
      }

      & img {
        width: 110px;
        height: 110px;
        border-radius: 50%;
        object-fit: cover;
        object-position: center top;

        @include on-tablet {
          width: 140px;
          height: 140px;
        }

        @include on-small-desktop {
          width: 110px;
          height: 110px;
        }

        @include on-large-desktop {
          width: 140px;
          height: 140px;
        }
      }

      &::before {
        position: absolute;
        content: '';
        display: block;
        z-index: -1;
        left: -25px;
        top: -40px;
        background: url(../images/testimonials/Rectangle-blue.png) no-repeat;
        width: 90px;
        height: 109px;
      }

      &::after {
        position: absolute;
        content: '';
        display: block;
        z-index: -1;
        right: -25px;
        bottom: -40px;
        background: url(../images/testimonials/Rectangle-blue.png) no-repeat;
        width: 90px;
        height: 109px;
      }
    }

    &-comment {
      display: flex;
      flex-direction: column;
      align-items: center;

      color: #6c788b;

      & img {
        padding-bottom: 8px;
      }
    }

    &-name {
      color: #253757;
    }

    &-profession {
      color: #c0cdd7;
      letter-spacing: 3px;
      font-weight: 700;
      font-family: poppins, Arial, Helvetica, sans-serif;
    }
  }
}
