.nav {
  &__burger-menu {
    display: block;
    left: 101px;
    position: absolute;
    top: 50%;

    @include on-small-desktop {
      display: none;
    }
  }

  &__wrapper {
    padding-top: 21px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }

  &__logo {
    width: 46px;
    height: 28px;
  }

  &__list {
    display: none;
    justify-content: space-between;
    list-style: none;

    @include on-small-desktop {
      display: flex;
    }
  }

  &__item {
    &:not(:first-child) {
      padding-left: 48px;
    }

    &--hire-us {
      position: relative;
    }

    &--hire-us::after {
      position: absolute;
      content: '';
      left: 0;
      right: 0;
      bottom: -8px;
      border-bottom: 2px solid #2060f6;
      border-radius: 4px;
    }
  }

  &__link {
    text-decoration: none;
    color: #fff;
    text-transform: uppercase;
    font-size: 13px;
    line-height: 150%;
  }
}
