.about-us {
  @include page-grid;

  text-align: center;

  padding-block: 120px;

  @include on-tablet {
    padding-block: 148px;
  }

  &__title {
    margin-bottom: 24px;
    grid-column: 1 / -1;

    @include on-small-desktop {
      grid-column: 2 / -2;
    }

    @include on-large-desktop {
      grid-column: 3 / -3;
    }

    & h2 {
      color: #253757;
      font-family: poppins, Arial, Helvetica, sans-serif;
      font-weight: 600;
      font-size: 32px;
      line-height: 150%;
    }
  }

  &__information {
    font-family: 'open sans', Arial, Helvetica, sans-serif;
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    color: #6c788b;
    grid-column: 1 / -1;

    @include on-small-desktop {
      grid-column: 2 / -2;
    }

    @include on-large-desktop {
      grid-column: 3 / -3;
    }
  }
}
