.header {
  width: 100%;
  height: 893px;
  background-color: $bg-header;
  border-radius: 0 0 30px 30px;
  position: relative;

  &__information {
    margin-block: 48px;
    color: white;

    @include on-small-desktop {
      margin-block: 164px;
      max-width: 490px;
    }

    &-title {
      font-size: 53px;
      line-height: 120%;
      font-weight: 600;
      letter-spacing: -2px;
      font-family: poppins, arial, sans-serif;
      margin-bottom: 32px;
    }

    &-text {
      font-family: 'open sans', Arial, Helvetica, sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 150%;
      max-width: 362px;
      margin-bottom: 72px;
    }

    &-learn-more {
      height: 50px;
      width: 280px;
      background-color: #2060f6;
      border-radius: 8px;
      color: white;
    }
  }

  &__photo-block {
    position: absolute;
    height: 390px;
    width: 100%;
    bottom: 0;
    border-radius: 30px;
    right: 0;
    overflow: hidden;

    @include on-small-desktop {
      height: 680px;
      width: 49%;
      border-radius: 30px 0;
    }

    @include on-large-desktop {
      width: 43%;
    }
  }

  &__photo {
    object-fit: cover;
    height: 390px;
    width: 100%;
    position: relative;

    &-change {
      position: absolute;
      display: flex;
      flex-direction: column;
      left: 20px;
      bottom: 24px;

      @include on-small-desktop {
        flex-direction: row;
      }

      &--text {
        color: #fff;
        margin-top: 16px;
        font-size: 14px;
        font-family: sans-serif;
        font-weight: 400;
        line-height: 140%;

        @include on-small-desktop {
          margin-left: 24px;
        }

        & span {
          font-family: poppins, Arial, Helvetica, sans-serif;
          font-size: 13px;
          line-height: 150%;
          letter-spacing: 3px;
          font-weight: 700;
          text-transform: uppercase;
        }
      }

      &--wrapper {
        display: flex;
        gap: 18px;
        align-items: center;
      }

      &--decoration {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 32px;
        width: 32px;
        background-color: #fff;
        border-radius: 50%;
      }

      // &--left{

      // }

      // &--right{

      // }
    }

    @include on-small-desktop {
      height: 680px;
    }
  }
}
