.our-expertise {
  text-align: center;
  font-size: 44px;
  font-weight: 600;
  grid-column: 1 / -1;

  &__wrapper {
    background-color: #fff;
    border-radius: 30px;

    padding: 72px 0;

    @include on-tablet {
      padding-top: 128px 0 128px;
    }
  }

  &__category {
    @include page-grid;

    justify-items: center;

    &--align-left {
      justify-self: left;
    }

    &--align-right {
      justify-self: right;
    }

    &--wrapper {
      grid-column: span 2;
      display: flex;
      align-items: center;
      flex-direction: column;
      cursor: pointer;

      @include hover(scale, 1.1);

      @include on-tablet {
        grid-column: 2 / -2;
      }

      @include on-small-desktop {
        width: 214px;
        grid-column: span 4;
      }

      @include on-large-desktop {
        width: 264px;
        grid-column: span 4;
      }
    }

    &--photo {
      margin: 56px 0 32px;
      border-radius: 16px;
      overflow: hidden;
      width: 166px;
      height: 166px;
    }

    &--title {
      font-family: poppins, Arial, Helvetica, sans-serif;
      font-weight: 600;
      font-size: 20px;
      line-height: 150%;
      margin-bottom: 16px;
      color: #253757;
    }

    &--information {
      color: #6c788b;
      font-family: 'open sans', Arial, Helvetica, sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      text-align: center;
    }
  }
}
