.footer {
  &__wrapper {
    height: 100%;
    padding: 72px 0 80px;
    background-color: #2c2c2c;
    border-radius: 20px 20px 0 0;
    color: #fff;
  }

  @include page-grid;

  &__message {
    display: flex;
    flex-direction: column;

    grid-column: 1 /-1;

    & button {
      @extend %button;
    }

    &-title {
      font-size: 32px;
      font-weight: 600;
      line-height: 150%;
    }

    &-input {
      background-color: #2c2c2c;
      height: 41px;
      width: 100%;
      box-sizing: border-box;
      margin-bottom: 44px;
      border: hidden;
      border-bottom: 1px solid white;
      color: white;
      font-size: 15px;
      line-height: 150%;
      font-weight: 600;
      outline: 0;

      &::placeholder {
        color: white;
        opacity: 0.7;
      }

      &:hover {
        border-bottom: 1px solid #144ed4;
      }
    }
  }

  &__contact {
    grid-column: 1 / -1;

    &-title {
      font-size: 32px;
      font-weight: 600;
      line-height: 150%;
      color: #fff;
    }

    &-information {
      & h3 {
        font-size: 13px;
        line-height: 150%;
        letter-spacing: 3px;
        font-weight: 700;
        opacity: 0.5;
      }

      & a,
      & p {
        margin: 0;
        text-decoration: none;
        font-size: 24px;
        color: #fff;
        font-weight: 600;
      }

      &:nth-of-type(-n + 2) p {
        margin-bottom: 40px;
      }

      &-image {
        object-fit: contain;
        object-position: center;
        width: 32px;
        height: 32px;
        margin-right: 32px;
      }
    }
  }

  &__menu {
    display: flex;
    flex-direction: column;
    margin-top: 80px;
    grid-column: span 2;

    @include on-tablet {
      margin-top: 139px;
      grid-column: 1 / -1;
    }

    @include on-small-desktop {
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
    }

    &-logo {
      display: block;
      width: 48px;
      height: 28px;
    }

    &-list {
      margin-top: 48px;
      padding: 0;
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: start;

      @include on-tablet {
        flex-direction: row;

        & :not(:last-child) {
          margin-right: 64px;
        }
      }

      @include on-small-desktop {
        justify-content: end;
      }
    }

    &-link {
      color: #fff;
      text-decoration: none;
    }

    &-item {
      list-style: none;
      size: 13px;
      line-height: 150%;
      letter-spacing: 3px;
      font-weight: 700;
      margin: 0 0 24px;
    }
  }
}
